import s from "./Home.module.css";
import React from "react";
import { work_data } from "../../components/data";
import WorkItem from "../../components/WorkItem/WorkItem";
import RecentPosts from "../../components/RecentPosts/RecentPosts";
import HelloComponent from "../../components/HelloComponent/HelloComponent";

export default function Home() {
    return (
        <div >
            <HelloComponent/>
            <div className={s.recent_posts}><RecentPosts/></div>
            <div className={`${s.works__container} wrapper_page_home`}>
                <p className={s.desc_title}>Featured works</p>
                {work_data.map((el) => {
                    return (
                        <WorkItem
                            key={el.id}
                            id={el.id}
                            title={el.title}
                            full_title={el.full_title}
                            main_img={el.main_img}
                            tags={el.tags}
                            text={el.text}
                            year={el.year}
                            content={el.content}
                        />
                    );
                })}
            </div>
        </div>
    );
}
