import s from './Footer.module.css';
import React from 'react'
import fb_img from '../../images/icon_fb.svg'
import linkedin_img from '../../images/icon_linkedin.svg'
import twitter_img from '../../images/icon_twitter.svg'
import instagram_img from '../../images/icon_instagram.svg'

export default function Footer() {
  return (
    <div className={s.footer}>
      <div className={s.images}>
          <a href="https://www.facebook.com" target='_blank'><img src={fb_img} alt="fb" /></a>
          <a href="https://www.linkedin.com" target='_blank'><img src={linkedin_img} alt="linkedin" /></a>
          <a href="https://twitter.com" target='_blank'><img src={twitter_img} alt="twitter" /></a>
          <a href="https://www.instagram.com" target='_blank'><img src={instagram_img} alt="instagram" /></a>
      </div>
      <p>Copyright ©2020 All rights reserved </p>
    </div>
  )
}
